   --lilac-color: #ad7ffa;\n}\n\nbutton {\n    font-weight: bolder;\n}\n\nimg {\n    max-width: 100%;\n}\n\n.wrapper {\n  display: grid;\n  width: 100vw;\n  height: 100vh;\n  grid-template-columns: 1fr;\n  grid-template-rows: 80px 1fr 50px;\n  grid-template-areas: \n  \"header\"\n  \"main\"\n  \"footer\";\n  /*row-gap: 15px;*/\n  column-gap: 15px;\n  color: #f7f7f7;\n}\n\n.header {\n  grid-area: header;\n  display: flex;\n  justify-content: space-around;\n  align-items: center;\n  text-align: center;\n}\n\n.logo {\n  font-size: 2rem;\n  font-weight: bolder;\n}\n\n.logo-index {\n  font-size: 2rem;\n  font-weight: bolder;\n  width: 50%;\n}\n\n.header-bttn {\n  width: 10%;\n}\n\n.header-bttn-index {\n}\n\n.main {\n  grid-area: main;\n  height: 552px;\n  display: grid;\n  grid-template-columns: minmax(450px, 1fr) minmax(300px, 40vw);\n  grid-template-areas: \n  \"left-main right-main\";\n  column-gap: 15px;\n  background: #f7f7f7;\n  margin: 0px 20px 0px 20px;\n  border-radius: 30px;\n  box-shadow: 0 10px 20px -8px rgb(0 0 0 / 70%);\n}\n\n.left-main {\n  grid-area: left-main;\n  /* height: 450px; */\n  padding: 20px 20px 20px 35px;\n  display: flex;\n  flex-direction: column;\n  background-image: url(../images/background5.jpg);\n  background-repeat: no-repeat;\n  background-size: cover;\n  background-position: center;\n  position: relative;\n  border-radius: 30px;\n  box-shadow: 0 10px 20px -8px rgb(0 0 0 / 70%);\n}\n\n.left-picture {\n  flex: 1;\n  height: 100%;\n  background: url(../images/abank.png);\n  background-repeat:no-repeat;\n  background-size: contain;\n  background-position: center;\n  position: relative;\n}\n\n.left-content {\n  /*flex: 0;\n  height: 250px;*/\n  border-radius: 15px;\n  color: ivory;\n  font-weight: bold;\n  padding-left: 30px;\n  padding-right: 30px;\n  /* justify-self: center; */\n  /* background: 0px, 0px, 0px; */\n  background-color: rgba(0, 0, 0, 0.4);\n}\n\n.text-lines {\n  display: flex;\n  flex-direction: column;\n  padding: 5px 0px 